import "./roastinglist.css";
import { DataGrid } from '@mui/x-data-grid';
import { FormControl, Grid, TextField, InputLabel,Container,Button,Typography } from "@mui/material";
import { useParams,useNavigate,useLocation } from "react-router-dom";

import { useState, useEffect } from "react";
import axios from "axios"

export default function OrderList() {
  const [orders, setOrder] = useState([]);
  const [beans, setBeans] = useState([]);
  const [loading, setLoading] = useState(true);
  let params = useParams();
  const location = useLocation()
  useEffect( () => {

    const fetchData = async () => {
      const result = await axios.post(
        "https://glitchhub.coffee/api/v1/recipes/getRoastingList",
        {
          orderIDs:location.state
        }
      );
        console.log(result.data)
      setLoading(false)
      setOrder(result.data[1]);
      setBeans(result.data[0]);
    };

    console.log(location.state)
    fetchData();

  },[]);

  function ShowList(){
    if(!orders.length===0){
      setLoading(true)
    }

  }

  const columnsProducts = [
    { field: "id", headerName: "Product", width: 350 },
    {
      field: "amount",
      headerName: "QTY",
      width: 100,
    },
  ];



  const columns = [
    { field: "id", headerName: "BEAN", width: 250 },
    {
      field: "amount",
      headerName: "QTY (KG)",
      width: 150,
    },
  ];
  const [sortModel, setSortModel] = useState([
    {
      field: 'id',
      sort: 'asc',
    },
  ]);

  return (
    
    <div className="productList">

      <div className="tables">
        <div className="roastingList">
          <DataGrid
          rows={beans}
          disableSelectionOnClick
          pageSize={20}
          columns={columns}
          sortModel={sortModel}
          onSortModelChange={(model) => setSortModel(model)}
          loading = {loading}
          />
        </div>

        <div className="productsList">
          <DataGrid
          rows={orders}
          disableSelectionOnClick
          pageSize={20}
          columns={columnsProducts}
          sortModel={sortModel}
          onSortModelChange={(model) => setSortModel(model)}
          loading = {loading}
          />
        </div>

        <div>
          hello
        </div>

      </div>
    </div>
  );
}