import "./orderlist.css";
import { DataGrid } from '@mui/x-data-grid';
import { FormControl, Grid, TextField, InputLabel,Container,Button,Typography } from "@mui/material";
import { Link } from "react-router-dom";

import { useState, useEffect } from "react";
import axios from "axios"

export default function ProductList() {
  const [orders, setOrder] = useState([]);
  const [select, setSelection] = useState([]);
  const [selectedOrders, setSelectedOrders] = useState([])

  useEffect( () => {

    const fetchData = async () => {
      const result = await axios(
        "https://glitchhub.coffee/api/v1/orders",
      );

      setOrder(result.data);
    };
    fetchData();

  },[]);

  const handleSelection = (item) =>{
    setSelectedOrders(item)
  }
  
  const calculateRoastingList = () =>{
    if (selectedOrders.length === 0){
      alert("Select at least one order!")
    }
    else{
      

    }
  }
  


  const columns = [
    { field: "id", headerName: "Order ID", width: 90 },
    {
      field: "customerName",
      headerName: "Customer",
      width: 150,
    },
    {
      field: "date",
      headerName: "Date",
      width: 107,
    },
    {
      field: "products",
      headerName: "Products",
      width: 20000,
      renderCell: (params) => {
        return (
          <div className="test">
            {params.row.products.map(product => (              
                <span><b>{product.name}</b> <b>{product.id}</b> - {product.amount} </span> 
              
          ))}
      
          </div>
        );
      },
    }

  ];
  const [sortModel, setSortModel] = useState([
    {
      field: 'id',
      sort: 'desc',
    },
  ]);
  useEffect(()=>{

    
    console.log(selectedOrders)

  },[selectedOrders])

  function Test(){


      if (selectedOrders.length>0){
        return(
          <Link
            to='/roastingList'
            state={selectedOrders}
          >
            <Button
              className=""
              onClick= {calculateRoastingList}
              color="primary" 
              variant="contained">
              Calculate Orders
            </Button>
          </Link>
        )

      }
      else{
        return(
          <Button
          disabled
          className=""
          onClick= {calculateRoastingList}
          color="primary" 
          variant="contained">
          Calculate Orders
        </Button>
        )

      }
  

  }

  return (
    
    <div className="productList">

      <div className="calculateButton">
        <Test/>

      </div>
      <br/>
        <DataGrid
          rows={orders}
          disableSelectionOnClick
          columns={columns}
          pageSize={40}
          checkboxSelection
          sortModel={sortModel}
          onSelectionModelChange= {item => handleSelection(item)}
          />
    </div>
  );
}
