import "./recipes.css";
import { DataGrid } from '@mui/x-data-grid';
import { Outlet } from "react-router-dom";

import { useState, useEffect } from "react";
import axios from "axios"
import { Link } from "react-router-dom";
import DeleteIcon from '@mui/icons-material/Delete'; 
export default function ProductList() {


  const [orders, setOrder] = useState([]);


  const handleDelete = (id) => {
    setOrder(orders.filter((item) => item.id !== id));
    axios.delete("https://glitchhub.coffee/api/v1/recipes/"+id)
  };

  useEffect( () => {

    const fetchData = async () => {
      const result = await axios(
        "https://glitchhub.coffee/api/v1/recipes",
      );

      setOrder(result.data);
    };
    fetchData();

  });

  const buttonE = () =>{
    return (<h1>test</h1>)
  }




  const columns = [
    { field: "id", headerName: "Product", width: 400,editable: true },
    {
      field: "edit", 
      headerName:"Edit",
      renderCell:(params)=>{
        return(
          <Link
          to={`/recipe/${params.row._id}`}
          key={params.row._id}
        >
          <button className="productListEdit">Edit</button>
          </Link>
        )
      }
    },
    {
      field: "action",
      headerName: "Delete",
      width: 150,
      renderCell: (params) => {
        return (
          <>
            <DeleteIcon
              className="productListDelete"
              onClick={() => handleDelete(params.row.id)}
            />
          </>
        );
      },
    },
    { field: "Create", 
    headerName:           
      <Link to={`/recipe/newRecipe`}>
        <div> 
          <button className="newRecipe">Create Recipe</button>
        </div>
      </Link> ,width: 200,editable: true },
  ];

  return (
    
    <div className="productList">

      {/* <ul>
        {orders.map(order => (
          <li key={order.id}>
            <p>{order.id}, {order.date}</p>
          </li>
        ))}
      </ul> */}
        
        <DataGrid
          rows={orders}
          disableSelectionOnClick
          columns={columns}
          pageSize={20}
          checkboxSelection
        />

    </div>
  );
}
